@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100&display=swap);
.body {
  height: 120vh;
}

.App {
  text-align: center;
}
.section {
  width: 100%;
}

.section > div {
  /* height: inherit; */
  margin: 0 auto;
}

/* PC */
@media (min-width: 768px) {
  .section > div {
    width: 1200px;
  }
}

/* 모바일 */
@media (max-width: 768px) {
  .section > div {
    width: 100%;
  }
}

.nav {
  background-color: #3773d4;
  margin-bottom: 10px;
}

.nav .nav-menu a,
.nav .btn {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  color: #b0c5ff;
}

.nav .selected a {
  color: white;
}

.nav .btn {
  background-color: #ffffff;
  color: #3773d4;
  display: block;
  outline: none;
  border: none;
}

/* PC */
@media (min-width: 768px) {
  .nav {
    margin-bottom: 80px;
  }

  .nav .wrapper {
    width: 1200px;
    position: relative;
    margin: 0 auto;
  }

  .nav .nav-menu {
    box-sizing: border-box;
    display: inline-block;
    width: 200px;
    height: 90px;
  }

  .nav .nav-menu :hover {
    color: #ffffff;
    border-bottom: 2px solid #ffffff;
  }

  .nav .nav-menu a {
    line-height: 90px;
    font-size: 30px;
  }

  .nav .btn {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 18px;
    width: 130px;
    line-height: 50px;
    border-radius: 100px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
  }
}

/* 모바일 */
@media (max-width: 768px) {
  .nav {
    margin-bottom: 10vh;
  }

  .nav .wrapper {
    width: 100%;
  }

  .nav .nav-menu {
    height: 40px;
    line-height: 40px;
  }

  .nav .btn {
    width: 100%;
    line-height: 40px;
    font-weight: 900;
    font-size: inherit;
  }

  .nav .selected a {
    font-weight: 550;
  }
}

.main-title {
  height: 58px;
  text-shadow: 5px 5px 20px rgba(0, 0, 0, 0.2);
  font-family: Roboto, sans-serif;
  font-size: 50px;
  font-weight: 700;
  line-height: 1.16;
  letter-spacing: -2.5px;
  color: #3773d4;
  margin-bottom: 40px;
}

/* 모바일 */
@media (max-width: 768px) {
  .main-title {
    margin-top: -10%;
    margin-bottom: 20px;
  }
}

.footer {
  margin: 0 auto;
}

.footer p {
  font-family: "Roboto", sans-serif;
  font-weight: 320;
}

/* PC */
@media (min-width: 768px) {
  .footer {
    width: 1200px;
    margin-top: 80px;
  }
}

/* 모바일 */
@media (max-width: 768px) {
  .footer {
    width: 100%;
    margin-top: 10vh;
  }

  .footer p {
    font-size: 12px;
  }
}

.main .profile-img {
  background: url(/static/media/profileImg-test.f080f1f4.jpg) no-repeat center/400px;
  width: 400px;
  height: 400px;
  margin: 0 auto;
